/**
 * ä¸º Conversation å®ä¹ä¸ä¸ªæ°å±æ§
 * @param {String} prop å±æ§å
 * @param {Object} [descriptor] å±æ§çæè¿°ç¬¦ï¼åè§ {@link https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Object/getOwnPropertyDescriptor#Description getOwnPropertyDescriptor#Description - MDN}ï¼é»è®¤ä¸ºè¯¥å±æ§åå¯¹åºç Conversation èªå®ä¹å±æ§ç getter/setter
 * @returns void
 * @example
 *
 * conversation.get('type');
 * conversation.set('type', 1);
 *
 * // equals to
 * defineConversationProperty('type');
 * conversation.type;
 * conversation.type = 1;
 */
const defineConversationProperty = (
  prop,
  descriptor = {
    get() {
      return this.get(prop);
    },
    set(value) {
      this.set(prop, value);
    },
  }
) => {
  Object.defineProperty(Conversation.prototype, prop, descriptor);
};

